@import url('https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap');

* {
  box-sizing: border-box;
}

.title {
  font-family: 'Permanent Marker', cursive;
  font-size: 1.5rem;
}

.board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

#board {
  display: flex;
  flex-direction: column;
  width: 290px;
  height: 290px;
  border: 1px solid black;
}

.row {
  display: flex;
  flex-shrink: 0;
  width: 288px;
  height: 32px;
}

.cell {
  text-align: center;
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  border: 1px solid black;
}

.section-wall {
  border-right-width: 3px;
}

.section-floor {
  border-bottom-width: 3px;
}

.invalid {
  background-color: pink;
}

.button-container {
  display: flex;
  justify-content: space-between;
}

.button {
  height: 40px;
  width: 45%;
  flex-shrink: 0;
  margin-top: 15px;
  background-color: #ffffff;
  border: 3px solid #000000;
  font-weight: bold;
  font-size: 20px;
}

.error {
  height: 2rem;
  font-family: 'Permanent Marker', cursive;
  font-size: 1.2rem;
  color: red;
  text-align: center;
}

.details {
  font-family: 'Permanent Marker', cursive;
  font-size: 1rem;
  margin-top: 1rem;
}

@media (min-width: 600px) {
  .board-container {
    justify-content: flex-start;
  }
  .title {
    font-size: 3rem;
  }
  #board {
    width: 580px;
    height: 580px;
  }

  .row {
    width: 576px;
    height: 64px;
  }

  .cell {
    width: 64px;
    height: 64px;
    font-size: 2rem;
  }

  .section-wall {
    border-right-width: 6px;
  }

  .section-floor {
    border-bottom-width: 6px;
  }

  .details {
    font-size: 1.2rem;
    margin-top: 0rem;
  }
}